.input {
    position: relative;
    display: block;
    width: 100%;
    border: none;
    background-color: transparent;
    color: var(--Primary-Primary, #000);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
}

.disabled {
    color: var(--Tertiary, #666);
}
